.font-lato {
    font: 18px 'Lato', 'HelveticaNeue', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    white-space: normal;
    text-decoration: none;
}

.flex {
    display: flex;
}

.flex-wrap {
    flex-wrap: wrap;
}

.justify-content--space-between {
    justify-content: space-between;
}

.justify-content--center {
    justify-content: center;
}

.time {
    font-family: "Courier New", Courier, monospace;
}

.basic-info-item {
    margin-bottom: 10px;
    padding-right: 2rem;
}

.App {
    max-width: 70.875rem;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: flex-start;
    align-content: stretch;
    padding: 1rem;
}

#name {
    padding-bottom: 6px;
    padding-top: 20px;
    font-size: 1.7em;
    font-weight: bold;
    letter-spacing: 4px;
    color: rgba(0, 0, 0, 0.72);
}

#title {
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: rgba(0, 0, 0, 0.62);
    margin-bottom: 1rem;
}

.icon img {
    fill: black;
    width: 1.4rem;
    height: 1.4rem;
    margin-right: .4rem;
    display:inline-block;
    vertical-align: middle;
}

.photo {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    -webkit-print-color-adjust: exact;
    margin-right: 3rem;
}

.photo img {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
}

@media (max-width: 378px) {
    .App > div {
        flex-direction: column-reverse;
    }
}

@media (min-width: 600px) {
    .font-lato {
        font-size: 18px;
    }
    .App {
        border: 1px #e1e4e8 solid;
        border-radius: 5px;
        margin-top: 2rem;
        padding: 3rem;
    }
}

@media print {
    .App {
        border: none;
    }
}


